import http from "../http-common";

class DataService {

  login(email: string, password: string) {
    let data = new FormData();
    data.append("email", email);
    data.append("password", password);
    return http.post("/login", data, {withCredentials: true});
  }

  logout() {
    return http.get("/logout");
  }

  get(id: string) {
    return http.get(`/v1/user/${id}`, {withCredentials: true});
  }

  create(data: any) {
    return http.post("/v1/user", data, {withCredentials: true});
  }

  active(id: string, active: boolean) {
    return http.put(`/v1/user/${id}/active`, {active: active}, {withCredentials: true});
  }

  drink(id: string) {
    return http.put(`/v1/user/${id}/drink`, null , {withCredentials: true});
  }

  undoDrink(id: string) {
    return http.put(`/v1/user/${id}/drink/undo`, null , {withCredentials: true});
  }

  pay(id: string, amount: number) {
    return http.put(`/v1/user/${id}/pay?amount=${amount}`, null, {withCredentials: true})
  }

  update(id: string, data: any) {
    return http.put(`/v1/user/${id}/userinfo`, data, {withCredentials: true});
  }

  changePassword(id: string, password: string) {
    return http.put(`/v1/user/${id}/password`, {password: password}, {withCredentials: true});
  }

  delete(id: string) {
    return http.delete(`/v1/user/${id}`, {withCredentials: true});
  }

  findWithFilter(active: boolean, filter: string) {
    let url = `/v1/user?active=${active}`;
    if (filter.length > 0) {
      url += `&filter=${filter}`;
    }
    return http.get(url, {withCredentials: true});
  }

  getStats(active: boolean, filter: string, year: number) {
    let url = `/v1/stats?active=${active}`;
    if (filter.length > 0) {
      url += `&filter=${filter}`;
    }
    if (year  > 0) {
      url += `&year=${year}`
    }
    return http.get(url, {withCredentials: true});
  }

  getMatches(tournament: boolean, limit: number) {
    let url = `/v1/match?tournament=${tournament}`;
    if (limit  > 0) {
      url += `&limit=${limit}`
    }
    return http.get(url, {withCredentials: true});
  }

  saveMatch(data: any) {
    return http.post("/v1/match", data, {withCredentials: true});
  }

  saveTournament(data: any) {
    return http.post("/v1/match/tournament", data, {withCredentials: true});
  }

  getTableaux(tournament: boolean, year: number, limit: number) {
    let url = `/v1/match/tableaux?tournament=${tournament}`
    if (year  > 0) {
      url += `&year=${year}`
    }
    if (limit > 0) {
      url += `&limit=${limit}`
    }
    return http.get(url, {withCredentials: true})
  }

  updateSettings(data: any) {
    return http.post("/v1/settings", data, {withCredentials: true});
  }

  getSettings() {
    return http.get("/v1/settings", {withCredentials: true})
  }

}

export default new DataService();