import Vue from "vue";
import VueRouter, { RouteConfig } from "vue-router";
import DataService from "../services/DataService";
import http from "../http-common";

Vue.use(VueRouter);


const ifAuthenticated = (to: any, from: any, next: any) => {
  if (localStorage.getItem("authToken")) {
    next();
    return;
  }
  next("/signin");
};

const ifNotAuthenticated = (to: any, from: any, next: any) => {
  if (!localStorage.getItem("authToken")) {
    next();
    return;
  }
  next("/");
};

const logout = (to: any, from: any, next: any) => {
  DataService.logout();
  localStorage.removeItem("authToken");
  delete http.defaults.headers.common['Authorization'];
  next();
}

const routes: Array<RouteConfig> = [
  {
    path: "/signin",
    name: "signin",
    component: () => import("../components/Login.vue"), 
    beforeEnter: ifNotAuthenticated
  },
  {
    path: "/signout",
    name: "signout",
    component: () => import("../components/Login.vue"), 
    beforeEnter: logout
  },
  {
    path: "/",
    alias: "/kickers",
    name: "kickers",
    component: () => import("../components/KickerList.vue"),
    beforeEnter: ifAuthenticated
  },
  {
    path: "/edit/:id",
    name: "kicker-details",
    component: () => import("../components/Kicker.vue"),
    beforeEnter: ifAuthenticated
  },
  {
    path: "/add",
    name: "add",
    component: () => import("../components/AddKicker.vue"),
    beforeEnter: ifAuthenticated
  },
  {
    path: "/match",
    name: "match",
    component: () => import("../components/Match.vue"),
    beforeEnter: ifAuthenticated
  },
  {
    path: "/tournament",
    name: "tournament",
    component: () => import("../components/Tournament.vue"),
    beforeEnter: ifAuthenticated
  },
  {
    path: "/stats",
    name: "stats",
    component: () => import("../components/Stats.vue"),
    beforeEnter: ifAuthenticated
  },
  {
    path: "/settings",
    name: "settings",
    component: () => import("../components/Settings.vue"),
    beforeEnter: ifAuthenticated
  }
];

const router = new VueRouter({
  // mode: "history",
  mode: "hash",
  base: process.env.BASE_URL,
  routes
});

export default router;
