import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import { library } from '@fortawesome/fontawesome-svg-core';
import { faBeer, faUserPlus, faEuroSign, faFutbol, faTrophy, faChartLine, faCog, faUserCog, faSignOutAlt, faUndo } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';
import http from "./http-common";

library.add(faBeer, faUserPlus, faEuroSign, faFutbol, faTrophy, faChartLine, faCog, faUserCog, faSignOutAlt, faUndo);
Vue.component('font-awesome-icon', FontAwesomeIcon);

Vue.config.productionTip = false;

const token = localStorage.getItem('authToken');
if (token) {
  http.defaults.headers.common['Authorization'] = token;
}

new Vue({
  router,
  render: h => h(App)
}).$mount("#app");
